import React, { useEffect, useState } from 'react'
import { useStateValue } from './StateProvider';
import './components/css/Home.css';
import ChatDialog from './components/js/ChatDialog';
import Header from './components/js/Header';
import { Route, Switch } from 'react-router-dom';
import Login from './pages/Login';
import Signup from './pages/Signup';
import Profile from './pages/Profile';
import Home from './pages/Home';

const App = () => {
  const [{ isChat, isLogin }, dispatch] = useStateValue();
  const [login, setLogin] = useState(false);

  useEffect(() => {
    const currentuser = localStorage.getItem('currentuser');
    if (currentuser) {
      const { isLogin } = JSON.parse(currentuser);
      setLogin(isLogin);
    }
  }, [login])

  return (
    <div>

      <Switch>
        <Route exact path="/webchat" >
          {login ?
            <div>
              <Header className='appbar' setLogin={setLogin} />
              <Home />
            </div>
            : <Login setLogin={setLogin} />
          }
        </Route>
        <Route path="/profile"  >
          <Header className='appbar' />
          <Profile />
        </Route>
      </Switch>
      {isChat ? <ChatDialog /> : null}
    </div>
  )

}
export default App;
