import React, { useState } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import likeone from '../images/like.gif';
import heart from '../images/heart.gif';
import angry from '../images/angry.gif';
import care from '../images/care.gif';
import wow from '../images/wow.gif';
import haha from '../images/haha.gif';
import sad from '../images/sad.gif';
import Tippy from '@tippy.js/react';
import { Box } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    alignItems: 'center',
    borderRadius: '1rem',
    overflow: 'hidden',
    width: 'fit-content',
    height: '3.5rem',
  },
  icons: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    width: 'fit-content',
    height: '2.5rem',
    borderRadius: '50%',
    '&:hover': {
      transform: 'scale(1.2)',
      outline: 'none',
    },
    paddingLeft: '0.2rem',
    paddingRight: '0.2rem',
    outline: 'none',
    border: 'none',
  },
  gifIcon: {
    width: '2.5rem', height: '2.5rem',

  }
}));

const StyledMenu = withStyles({
  paper: {
    display: 'flex',
    border: '1px solid #d3d4d5',
    padding: 0,
    margin: 0,
    overflow: 'hidden',
    borderRadius: '2rem',
    flexDirection: 'row',
    paddingLeft: '0.5rem',
    paddingRight: '0.5rem',
  },
})((props) => (
  <Menu
    elevation={0}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'center',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'center',
    }}
    {...props}
  />
));

const StyledMenuItem = withStyles((theme) => ({
  root: {
    transition: 'all 0.3s ease',
    '&:focus': {
      backgroundColor: theme.palette.common.white,
      '& .MuiListItemIcon-root': {
        // color: theme.palette.common.white,
      },
    },
    '&:hover': {
      transform: 'scale(1.2)',
      outline: 'none',
    },
    borderRadius: '50%',
    padding: '0rem',
    marginLeft: '0.3rem',
    marginRight: '0.3rem',
  },
}))(MenuItem);


function LikeIconsMenu({ anchorEl, setAnchorEl }) {
  const classes = useStyles();
  const [emo, setEmo] = useState(null);

  // const handleClick = (event) => {
  //   setAnchorEl(event.currentTarget);
  // };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleIcons = (e) => {
    const item = e.currentTarget.dataset.item;
    switch (item) {
      case 'like':
        setEmo({ name: 'like', count: 1 })
        break;
      case 'heart':
        setEmo({ name: 'heart', count: 1 })
        break;
      case 'care':
        setEmo({ name: 'care', count: 1 });
        break;
      case 'haha':
        setEmo({ name: 'haha', count: 1 });
        break;
      case 'wow':
        setEmo({ name: 'wow', count: 1 });
        break;
      case 'sad':
        setEmo({ name: 'sad', count: 1 });
        break;
      case 'angry':
        setEmo({ name: 'angry', count: 1 });
        break;

      default:
        break;
    }
    setAnchorEl(null);
  }

  return (
    <div className={classes.root}>
      <StyledMenu
        id="customized-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <div
          style={{ display: 'flex', outline: 'none' }}
        >
          <Tippy content='Like'>
            <StyledMenuItem onClick={handleIcons} data-item='like' >
              <img src={likeone} alt="" className={classes.gifIcon} />
            </StyledMenuItem></Tippy>

          <Tippy content='Love'><StyledMenuItem onClick={handleIcons} data-item='heart' >
            <img src={heart} alt="" className={classes.gifIcon} />
          </StyledMenuItem></Tippy>

          <Tippy content='Care' ><StyledMenuItem onClick={handleIcons} data-item='care' >
            <img src={care} alt="" className={classes.gifIcon} />
          </StyledMenuItem></Tippy>

          <Tippy content='Haha' ><StyledMenuItem onClick={handleIcons} data-item='haha' >
            <img src={haha} alt="" className={classes.gifIcon} />
          </StyledMenuItem></Tippy>

          <Tippy content='Wow'><StyledMenuItem onClick={handleIcons} data-item='wow' >
            <img src={wow} alt="" className={classes.gifIcon} />
          </StyledMenuItem></Tippy>

          <Tippy content='Sad'><StyledMenuItem onClick={handleIcons} data-item='sad'>
            <img src={sad} alt="" className={classes.gifIcon} />
          </StyledMenuItem></Tippy>

          <Tippy content='Angry'><StyledMenuItem onClick={handleIcons} data-item='angry' >
            <img src={angry} alt="" className={classes.gifIcon} />
          </StyledMenuItem></Tippy>

        </div>

      </StyledMenu>
    </div>
  );
}

export default LikeIconsMenu;